.classify-content {
  width: 100%;
  height: auto;
}
.classify-content .classify-item {
  width: 100%;
  height: auto;
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
}
.classify-content .classify-item .classify-item-info h2 {
  font-size: 16px;
  color: #030303;
  font-weight: normal;
  line-height: 24px;
}
.classify-content .classify-item .classify-item-info small {
  font-size: 12px;
  line-height: 24px;
  color: #a8a8a8;
}
.classify-content .classify-item .classify-item-info p {
  font-size: 16px;
  line-height: 24px;
  color: #fd7c42;
}
.classify-content .classify-item .classify-item-info span {
  font-size: 12px;
  line-height: 24px;
  color: #a8a8a8;
