.topic {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.topic .topic-banner {
  width: 100%;
  height: 180px;
  overflow: hidden;
}
.topic .topic-banner > img {
  display: block;
  width: 100%;
  height: 180px;
}
.topic .topic-box {
  padding: 15px 10px;
  line-height: 32px;
  box-sizing: border-box;
  text-align: center;
  background-color: #fff;
}
.topic .topic-box img {
  display: inline-block;
  vertical-align: bottom;
}
.topic .topic-main {
  width: 100%;
  height: auto;
}



