.discovery {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.discovery .discovery-head {
  width: 100%;
  height: 46px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: #fff;
}
.discovery .book-item {
  height: 100px;
  width: 100%;
  padding: 0 10px;
  margin-top: 10px;
  box-sizing: border-box;
}
.discovery .book-item .bookInfo {
  width: 100%;
  height: auto;
  padding: 3px;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}
.discovery .book-item .bookInfo:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 0;
  width: 32px;
  height: 32px;
  background-image: url('http://oqcwstdev.bkt.clouddn.com/chose_ok.png');
  background-size: 32px 32px;
  background-position: center center;
  visibility: hidden;
}
.discovery .book-item .bookInfo img {
  display: block;
  width: 100%;
  height: 100px;
}
.discovery .book-item .bookInfo .book_review-item-book-info p {
  line-height: 50px;
}
.discovery .book-item .bookInfo .book_review-item-book-info span {
  color: #9e9e9e;
}
.discovery .book-item-selected .bookInfo {
  border: 2px solid #fd7c42;
}
.discovery .book-item-selected .bookInfo:after {
  visibility: visible;
}



