.vux-close {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  color: #999;
  width: 24px;
  height: 24px;
}
.vux-close:before,
.vux-close:after {
  content: '';
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: currentColor;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.vux-close:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.order {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.order .bookBasket-item-content {
  height: 110px;
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.order .bookBasket-item-content .bookInfo {
  width: 100%;
  height: auto;
  padding: 3px;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}
.order .bookBasket-item-content .bookInfo:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 0;
  width: 32px;
  height: 32px;
  background-image: url('http://oqcwstdev.bkt.clouddn.com/chose_ok.png');
  background-size: 32px 32px;
  background-position: center center;
  visibility: hidden;
}
.order .bookBasket-item-content .bookInfo img {
  display: block;
  width: 100%;
  height: 100px;
}
.order .bookBasket-item-content .bookInfo .book_review-item-book-info h3 {
  line-height: 36px;
  font-weight: normal;
}
.order .bookBasket-item-content .bookInfo .book_review-item-book-info span {
  line-height: 30px;
  color: #9e9e9e;
}
.order .order-dialog .weui-dialog {
  border-radius: 8px;
  padding-bottom: 8px;
}
.order .order-dialog .dialog-title {
  line-height: 30px;
  color: #666;
}
.order .order-dialog .vux-close {
  margin-top: 16px;
  margin-bottom: 16px;
}
.orderTitle {
  width: 100%;
  height: 18px;
  padding: 5px 0;
}
.orderTitle h2 {
  text-align: center;
  line-height: 18px;
  font-size: 18px;
  font-weight: 400;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #030303;
}
.order-info-item {
  width: 100%;
  height: 30px;
  padding: 0 15px;
  box-sizing: border-box;
}
.order-info-item span {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 12px;
  line-height: 30px;
  color: #666;
}
.order-info-item span.content {
  text-align: right;
}
.infoContent-qrcode {
  width: 100%;
  height: 180px;
  padding: 10px 0;
  box-sizing: border-box;
}



