.book-hot-wrap {
  background-color: #fff;
  width: 50%;
  height: 180px;
  padding: 10px;
  float: left;
  box-sizing: border-box;
  position: relative;
}
.book-hot-wrap:after {
  content: '';
  display: block;
  width: 64px;
  height: 64px;
  background-image: url(http://oncxzyx3a.bkt.clouddn.com/hot-bookIcon.png);
  background-size: 64px 64px;
  position: absolute;
  right: 0;
  top: 0;
}
.book-hot-wrap .bookbanner {
  display: block;
  width: 160px;
  height: 160px;
  margin: 0 auto;
}



