
.postOrder {
  width: 100%;
  height: auto;
}
.postOrder .bookBasket-item-content {
  height: 110px;
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.postOrder .bookBasket-item-content .bookInfo {
  width: 100%;
  height: auto;
  padding: 3px;
  box-sizing: border-box;
  border-radius: 3px;
  background-color: #fff;
  position: relative;
}
.postOrder .bookBasket-item-content .bookInfo:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 0;
  height: 0;
  width: 32px;
  height: 32px;
  background-image: url('http://oqcwstdev.bkt.clouddn.com/chose_ok.png');
  background-size: 32px 32px;
  background-position: center center;
  visibility: hidden;
}
.postOrder .bookBasket-item-content .bookInfo img {
  display: block;
  width: 100%;
  height: 100px;
}
.postOrder .bookBasket-item-content .bookInfo .book_review-item-book-info h3 {
  line-height: 50px;
  font-weight: normal;
}
.postOrder .bookBasket-item-content .bookInfo .book_review-item-book-info span {
  color: #9e9e9e;
}



