.user {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.user-info {
  width: 100%;
  height: auto;
  padding: 15px 0;
  background-color: #fff;
}
.user-info .user-avarta {
  width: 100%;
  height: 60px;
}
.user-info .user-avarta img {
  display: block;
  width: 60px;
  height: 60px;
  border: none;
  border-radius: 50%;
  margin: 0 auto;
}
.user-info .user-name {
  width: 100%;
  height: 24px;
  line-height: 36px;
  text-align: center;
  color: #030303;
}
.user-orders {
  margin-top: 16px;
  background-color: #fff;
}
.user-orders .user-orders-icon {
  font-size: 28px;
}
.user-grid {
  background-color: #fff;
}
.user-grid .user-comment {
  width: 100%;
  padding: 15px 0;
  text-align: center;
}
.user-grid .user-comment .title {
  display: block;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  color: #fd7c42;
}



