body,
html {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: #f3f2f0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app {
  width: 100%;
  height: 100%;
}
.router-view {
  width: 100%;
}
.vux-out-enter-active,
.vux-out-leave-active,
.vux-in-enter-active,
.vux-in-leave-active {
  will-change: transform;
  -webkit-transition: all 500ms;
  transition: all 500ms;
  height: 100%;
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-perspective: 1000;
          perspective: 1000;
}
.vux-out-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.vux-out-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-in-enter {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.vux-in-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.weui-tabbar__item.weui-bar__item_on .icon-user:before,
.weui-tabbar__item.weui-bar__item_on .icon-classify:before,
.weui-tabbar__item.weui-bar__item_on .icon-book:before,
.weui-tabbar__item.weui-bar__item_on .icon-discovery:before {
  color: #fd7c42;
}
.tabbar-round {
  position: absolute;
  left: 50%;
  height: 50%;
  display: block;
  width: 50px;
  height: 50px;
  margin-top: -5px;
  margin-left: -25px;
  border-radius: 50%;
  text-align: center;
  line-height: 60px;
  background-color: #fd7c42;
}
.tabbar-round .icon-scan {
  font-size: 24px;
  margin-left: 1px;
}
.pull-right {
  float: right;
}
.pull-left {
  float: left;
}
.text-center {
  text-align: center;
}



