.comment-item {
  width: 100%;
  height: auto;
  margin-top: 10px;
  padding: 10px 0;
  background-color: #fff;
}
.comment-item .comment-item-content-avarta {
  width: 100%;
  height: auto;
}
.comment-item .comment-item-content-avarta > img {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 0 auto;
}
.comment-item .comment-item-content-t {
  height: auto;
  width: 100%;
}
.comment-item .comment-item-content-t h3 {
  font-size: 15px;
  line-height: 28px;
  font-weight: normal;
  color: #a8a8a8;
}
.comment-item .comment-item-content-b {
  height: auto;
  width: 100%;
  padding: 5px 0;
}
.comment-item .comment-item-content-b p {
  font-size: 14px;
  line-height: 28px;
  color: #7c7371;
  overflow: hidden;
}
.comment-item .comment-item-content-f {
  height: auto;
  width: 100%;
}
.comment-item .comment-item-content-f p {
  font-size: 14px;
  line-height: 28px;
  color: #b0b0b0;
}
.comment-item .comment-item-content-f p span.i-group {
  float: right;
}



