.bookInfo {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.bookInfo .book-banner {
  width: 100%;
  height: 350px;
  padding: 10px 0;
  background-color: #fff;
}
.bookInfo .book-banner > img {
  display: block;
  width: 350px;
  height: 350px;
  margin: 0 auto;
}
.bookInfo .book-info-content {
  width: 100%;
  height: auto;
}
.bookInfo .book-info-content .book-info-content-t {
  width: 100%;
  height: auto;
  padding: 10px 15px;
  box-sizing: border-box;
  background-color: #fff;
}
.bookInfo .book-info-content .book-info-content-t h1 {
  font-size: 16px;
  font-weight: normal;
  line-height: 35px;
  color: #030303;
}
.bookInfo .book-info-content .book-info-content-t h1 span {
  float: right;
  font-size: 14px;
  line-height: 35px;
  color: #b0b0b0;
}
.bookInfo .book-info-content .book-info-content-t .book-author {
  font-size: 14px;
  line-height: 20px;
  color: #fd7c42;
}
.bookInfo .book-info-content .book-info-content-t .book-author small {
  float: right;
  font-size: 14px;
  color: #b0b0b0;
}
.bookInfo .book-info-content .book-info-content-b {
  width: 100%;
  height: auto;
  padding: 10px 15px;
  box-sizing: border-box;
  background-color: #fff;
}
.bookInfo .book-info-content .book-info-content-b h2 {
  font-size: 24px;
  color: #fd7c42;
}
.bookInfo .book-info-content .book-info-content-b h2 .icon-price {
  font-size: 13px;
}
.bookInfo .book-info-content .book-info-content-b h2 span {
  font-size: 14px;
  line-height: 35px;
  color: #b0b0b0;
  font-weight: normal;
  margin-left: 15px;
  text-decoration: line-through;
}
.bookInfo .book-info-content .book-info-content-f {
  width: 100%;
  height: auto;
  padding: 10px 0;
  background-color: #fff;
}
.bookInfo .book-info-content .book-info-content-f .book-info-content-f-item {
  text-align: center;
  font-size: 14px;
  color: #b0b0b0;
}
.bookInfo .book-info-content .bookInfo-tabs-content {
  height: 180px;
  overflow: auto;
}
.bookInfo .book-info-content .bookInfo-tabs-content p {
  font-size: 14px;
  text-indent: 2em;
  line-height: 28px;
  color: #b0b0b0;
}
.bookInfo .book-info-content .bookInfo-tabs-content ul {
  padding-left: 20px;
  list-style: disc;
}
.bookInfo .book-info-content .bookInfo-tabs-content ul li {
  display: block;
  padding: 5px 0;
  font-size: 14px;
  color: #b0b0b0;
  list-style: disc;
}



