.find {
  width: 100%;
}
.find .book_review-banner {
  width: 100%;
  height: 180px;
  overflow: hidden;
}
.find .book_review-banner > img {
  display: block;
  width: 100%;
  height: 180px;
}
.find .book_review-item {
  width: 100%;
  height: auto;
  padding: 10px 15px;
  box-sizing: border-box;
  margin-bottom: 10px;
  background-color: #fff;
}
.find .book_review-item .book_review-item-l > img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-top: 5px;
}
.find .book_review-item .book_review-item-r .book_review-item-h {
  width: 100%;
  height: auto;
}
.find .book_review-item .book_review-item-r .book_review-item-h h2 {
  font-size: 16px;
  color: #afafaf;
  margin: 0;
  line-height: 30px;
  font-weight: normal;
}
.find .book_review-item .book_review-item-r .book_review-item-b p {
  font-size: 16px;
  line-height: 30px;
  margin: 5px 0;
  color: #4c4c4c;
}
.find .book_review-item .book_review-item-r .book_review-item-b .book_review-item-book {
  width: 100%;
  height: auto;
  padding: 3px;
  border: 1px solid #d6d5d5;
  box-sizing: border-box;
  border-radius: 3px;
}
.find .book_review-item .book_review-item-r .book_review-item-b .book_review-item-book img {
  display: block;
  width: 100%;
  height: 80px;
}
.find .book_review-item .book_review-item-r .book_review-item-b .book_review-item-book .book_review-item-book-info p {
  line-height: 35px;
}
.find .book_review-item .book_review-item-r .book_review-item-b .book_review-item-book .book_review-item-book-info span {
  color: #9e9e9e;
}
.find .book_review-item .book_review-item-r .book_review-item-f {
  width: 100%;
  height: auto;
  padding: 5px 0;
  margin-top: 5px;
}
.find .book_review-item .book_review-item-r .book_review-item-f p {
  color: #9e9e9e;
}
.find .book_review-item .book_review-item-r .book_review-item-f span {
  float: right;
  color: #9e9e9e;
  margin-left: 10px;
}
.find .book_review-item .book_review-item-r .book_review-item-f span.isLove {
  color: #fd7c42;
}



