
.userCoupon-item {
  width: 100%;
  height: 140px;
  overflow: hidden;
  position: relative;
}
.userCoupon-item span {
  position: absolute;
  left: 50%;
  top: 50%;
  display: block;
  width: auto;
  padding: 0 10px;
  font-size: 40px;
  font-weight: bold;
  color: #fff;
}
.userCoupon-item span > .icon-price:before {
  font-size: 20px;
  color: #fff;
}
.userCoupon-item p {
  width: 100%;
  position: absolute;
  top: 12%;
  text-align: center;
  color: #fff;
  font-size: 14px;
}
.userCoupon-item.used:before {
  color: #d9d9d9;
}
.userCoupon-item:before {
  position: absolute;
  left: 50%;
  top: -53px;
  margin-left: -120px;
  font-size: 240px;
  z-index: 0;
}



