
.coupon {
  position: relative;
  width: 320px;
  height: 101px;
  margin: 0 auto 10px auto;
  background-size: 320px 101px;
  background-repeat: no-repeat;
  background-position: center center;
}
.coupon.coupon-20 {
  background-image: url(http://oqcwstdev.bkt.clouddn.com/takit_20.png);
}
.coupon.coupon-10 {
  background-image: url(http://oqcwstdev.bkt.clouddn.com/takit_10.png);
}
.coupon.coupon-5 {
  background-image: url(http://oqcwstdev.bkt.clouddn.com/takit_5.png);
}
.coupon span {
  position: absolute;
  left: 0;
  right: 0;
  display: block;
  width: 80px;
  height: 101px;
  padding: 20px 0;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
}
.coupon .deadline-wrap {
  position: absolute;
  bottom: 0;
  left: 120px;
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}



