.bookReviewDetails {
  position: absolute;
  top: 46px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background-color: #f3f2f0;
  overflow-x: hidden;
}
.bookReviewDetails .bookReviewDetails-t {
  width: 100%;
  height: auto;
  padding: 10px 15px;
  box-sizing: border-box;
  background-color: #fff;
}
.bookReviewDetails .bookReviewDetails-t img.avarta {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 0 auto;
}
.bookReviewDetails .bookReviewDetails-t h2 {
  font-size: 16px;
  color: #afafaf;
  margin: 0;
  line-height: 30px;
  font-weight: normal;
}
.bookReviewDetails .bookReviewDetails-t span.date {
  font-size: 14px;
  color: #afafaf;
  margin: 0;
  line-height: 30px;
}
.bookReviewDetails .bookReviewDetails-t .bookReviewDetails-t-content {
  padding: 10px 0;
}
.bookReviewDetails .bookReviewDetails-t .bookReviewDetails-t-content p {
  font-size: 16px;
  line-height: 30px;
  color: #4c4c4c;
}
.bookReviewDetails .bookReviewDetails-t .bookInfo {
  width: 100%;
  height: auto;
  margin: 5px 0;
  padding: 3px;
  border: 1px solid #d6d5d5;
  box-sizing: border-box;
  border-radius: 3px;
}
.bookReviewDetails .bookReviewDetails-t .bookInfo img {
  display: block;
  width: 100%;
  height: 100px;
}
.bookReviewDetails .bookReviewDetails-t .bookInfo .book_review-item-book-info p {
  line-height: 50px;
}
.bookReviewDetails .bookReviewDetails-t .bookInfo .book_review-item-book-info span {
  color: #9e9e9e;
}
.bookReviewDetails .bookReviewDetails-b {
  width: 100%;
  height: auto;
}
.bookReviewDetails .bookReviewDetails-b .comment-item {
  width: 100%;
  height: auto;
  padding: 10px 15px;
  background-color: #fff;
  box-sizing: border-box;
}
.bookReviewDetails .bookReviewDetails-b .comment-item img.avarta {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 0 auto;
}
.bookReviewDetails .bookReviewDetails-b .comment-item h2 {
  font-size: 16px;
  color: #afafaf;
  margin: 0;
  line-height: 30px;
  font-weight: normal;
}
.bookReviewDetails .bookReviewDetails-b .comment-item h2 span {
  font-size: 14px;
}
.bookReviewDetails .bookReviewDetails-b .comment-item p {
  font-size: 16px;
  line-height: 30px;
  color: #4c4c4c;
  margin: 5px 0;
}
.bookReviewDetails .actions {
  height: 50px;
  width: 100%;
  padding: 10px 0;
  background-color: #fff;
  box-sizing: border-box;
  text-align: center;
}
.bookReviewDetails .actions i {
  font-size: 24px;
}
.bookReviewDetails .actions span {
  font-size: 16px;
  display: inline-block;
  vertical-align: top;
  color: #979797;
}
.bookReviewDetails .actions span.isLove {
  color: #fd7c42;
}
.bookReviewDetails .popup-wrap {
  width: 100%;
  height: 100%;
}
.bookReviewDetails .popup-wrap .popupContent {
  width: 100%;
  height: 260px;
  box-sizing: border-box;
  background-color: #fff;
}
.bookReviewDetails .popup-wrap .popupContent .popupContent-h {
  width: 100%;
  height: 35px;
  padding: 0 15px;
  box-sizing: border-box;
}
.bookReviewDetails .popup-wrap .popupContent .popupContent-h span {
  color: #fd7c42;
  line-height: 35px;
}



